$bg:rgb(16,16,16);


.banner{
    height: 600px;
    background-color:$bg ;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 3rem;
    box-sizing: border-box;


    &::after{
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background: $bg;
        opacity: 0.4;
        top: 0%;
        left: 0%;
    }
    h1,p{
        color: white;
        font-size: 3rem;
        width: 50%;
        z-index: 10;
    }
    p{
        font-size: 1.2rem;
        letter-spacing: 2px;
        word-spacing: 2px;
        flex-direction: column;
        width: 40%;
    }
    >div{
        display: flex;

        button{
            padding: 1rem;
            width: 150px;
            outline: none;
            border: none;
            z-index: 10;
            border-radius: 5px;
            margin: 1rem;
            cursor: pointer;
            font-size: 1.2rem;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.6s;
            &:hover{
                opacity: 0.6;
            }

            &:nth-child(odd){
                background-color: rgb(51, 59, 5);

            }
            &:nth-child(even)
            {
                background-color: cadetblue;
            }
           svg {
            font-size: 1.7rem;
            margin:0 10px;

    
            }
    
        }

    }
    
}

.row{
    padding: 1rem;
    background-color: $bg;
    >h2{
        font-weight: 400;
        margin: 0.3rem;
    }
    >div{
        display: flex;
        overflow-x: auto;
        
    }
    

}
.card{
    width: 200px;
    margin: 0.3rem;
    cursor: pointer;
    transition: transfrom 0.5s;

    &:hover{
        transform: translateY(-10px);
    }

}